// Tabbed Layout Messenger
class Tabs {
	constructor(main, settings = {}){
		this.tabs = [];
		this.active = 0;
		this.addTab(main);
		this.wrapNames = false;
    
    this.key = undefined !== settings.key ? settings.key : 'room_id';
    this.onActivateTab = undefined !== settings.onActivateTab ? settings.onActivateTab : ()=>{};
	}

	getTabs(){
		return this.tabs;
	}

	addTab(data){
    console.log('Adding Tab : ', data);
    
    // Check whether the same tab exists
    let exists = false;
    let index = -1;
    let counter = 0;
    this.tabs.forEach(tab => {
      console.log(tab[this.key] === data[this.key]);
      if (tab[this.key] === data[this.key]){ // Same Room ID, Don't add it again
        exists = true;
        index = counter;
      }
      counter++;
		});
		

		if (!exists){ // Doesn't exist already, add it
      this.tabs.push(data);
			this.active = this.tabs.length - 1;
			
			if (this.tabs.length > 5){
				this.wrapNames = true;
			}

      this.toggleTabbedView();
      this.generateTabCode();
      if (undefined !== this.onActivateTab){
        this.onActivateTab(data);
      }
    } else { // Don't add it, just activate it if it is not already active
      if (this.active !== index){
				this.activateTab(index);
			}
    }
	}

	closeTab(index){
		console.log('Closing Tab : ', index);

		if (index === 0){
			console.log("Can't close main tab");
			return;
		}

		this.tabs.splice(index, 1);
		if (this.tabs.length <= 5){
			this.wrapNames = false;
		}
    
		if (this.active == index){ // If current tab is closed, activate previous tab
      if (this.active != 0){
        this.activateTab(this.active - 1);
      } else {
        this.activateTab(0);
      }
    } else if (index < this.active) {
      this.activateTab(this.active - 1, false);
    } else {
      this.toggleTabbedView();
      this.generateTabCode();
    }
	}

	activateTab(index, withCallback = true){
		console.log('Activating Tab : ', index);

		if (this.active === index){
			return;
		}

    this.active = index;
    this.toggleTabbedView();
    this.generateTabCode();
    
		// Replace the Messeging Dom Here
    if (withCallback){
      this.onActivateTab(this.tabs[index]);
    }
	}

	toggleTabbedView(){
		if (this.tabs.length > 1){
			document.querySelector('.person-header').classList.add('display-none');
			document.querySelector('.person-header-tabbed').classList.remove('display-none');
		} else {
			this.active = 0;
			document.querySelector('.person-header').classList.remove('display-none');
			document.querySelector('.person-header-tabbed').classList.add('display-none');
		}
	}

	generateTabCode(){
		if (this.tabs.length < 2){
			let $html = `	<div class="name content">
									<i class="fa fa-bars" id="DetailsOpenerBars" style="margin-right:5px;"></i>${this.tabs[0].name}
								</div>
								<div class="product-image sidebar">
									${ 
										'null' != this.tabs[0].image ? 
										`<img src="${this.tabs[0].image}" alt="Profile Picture">` : 
										`<div class='no-image' ${(undefined != this.tabs[0].color && null != this.tabs[0].color) ? 'style="background:#'+this.tabs[0].color+'"' : ''}>${this.tabs[0].name.slice(0,1)}</div>`
									}
								</div>`;

			let $tabbedHTML = `<div class="tab flex-custom">${$html}</div>`

			document.querySelector('.person-header').innerHTML = $html;
			document.querySelector('.person-header-tabbed').innerHTML = $html;
		} else {
			let $html = '';
			let increment = 0;
			this.tabs.forEach(element => {
				$html += `<div class="tab flex-custom ${increment == this.active ? 'active' : ''}" data-index="${increment}">
										<div class="product-image sidebar">
											${ 
												'null' != element.image ? 
												`<img src="${element.image}" alt="Profile Picture">` : 
												`<div class='no-image' ${(undefined != element.color && null != element.color) ? 'style="background:#'+element.color+'"' : ''}>${element.name.slice(0,1)}</div>`
											}
										</div>
										${increment === 0 ? `<i class="fa fa-bars" id="DetailsOpenerBars" style="margin-right:5px;"></i>` : ``}
										<div class="name content">
											${this.wrapNames ? (element.name.indexOf(' ') > 0 && element.name.indexOf(' ') < 10 ? element.name.slice(0,element.name.indexOf(' ')) : element.name.slice(0,6)) : element.name}
										</div>
										${increment !== 0 ? // Show close button except first tab
												`<div class="closer sidebar">
													<button class="btn close-button" data-close='${increment}'><i class="fa fa-times"></i></button>
												</div>` : ``}
									</div>`;

				increment++;
			});

			document.querySelector('.person-header-tabbed').innerHTML = $html;
		}
	}
}

window.MessageTabs = Tabs;